*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1{
    color:deeppink;
    margin-bottom: 10px;
}
.main{
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(to left bottom, #051937, #002364, #002a93, #002cc0, #1222eb);
}

.container {
    max-width: 600px;
    margin: 0rem auto;
    padding: 20px;
    border: 1.5px solid #270dba;
    border-radius: 5px;
    text-align: center;
    box-shadow: 0px 0px 4px black ;
    transform: translate(0%,30%);
}

.selector{
    display: flex;
    gap: 3rem;
    margin-bottom: 15px;
    justify-content: center;
    border-radius: 4px;
}

.dropdown {
    width: 26%;
    padding: 6px;
    font-size: 16px;

}

.textarea {
    height: 120px;
    width: 90%;
    padding: 10px;
    margin: 15px 10px;
    font-size: 16px;
    border-radius: 10px;
}

.btn {
    background-color: #00ff62;
    color: #170606;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
}

.t-heading {
    margin-top: 20px;
    color: darkorange;
}

.t-text {
    white-space: pre-line;
    /* Preserve line breaks */
}